:root {
    --blue: #1e90ff;
    --white: #fffffa;
}

*{
    user-select: none;
}

.contentbox{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    
    width: 100%;
    margin-top: 8vh;
}

.content{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: #0C0C0C;
    flex-direction: column;
    width: 40vw;
    height: 80vh;
    margin-top: 0vw;
    margin-bottom: 0vw;
    z-index: 2;
    border-radius: 1vw;
    margin-top: 5vh;
    margin-bottom: 5vh;
}

.content .header{
    width: 100%;
    height: 10%;
    background-color: rgba(0, 0, 255, 0);
    color: var(--white);
    font-family: Lucky;
    font-size: 2vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content .text{
    width: 100%;
    height: 90%;
    background-color: rgba(255, 0, 0, 0);
    color: var(--white);
    font-family: Poetsen;
    font-size: 1.5vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    align-items: center;
}

.programinglaunguages{
    width: 90%;
    background-color: rgba(178, 34, 34, 0);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.programinglaunguages li{
    margin-bottom: 1vw;
    user-select:text;
}