@import url('https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap');
@font-face {
    font-family: Poetsen;
    src: url("./assets/fonts/PoetsenOne-Regular.ttf") format("truetype");
}
@font-face {
    font-family: Lucky;
    src: url("./assets/fonts/LuckiestGuy-Regular.ttf") format("truetype");
}
.upperbar{
    display: flex;
    flex-direction: row;
    position: fixed;
    width: 100%;
    height: 8vh;
    background-color: #0C0C0C;
    background: linear-gradient(0deg, rgba(0,0,0,0.9206057422969187) 0%, rgba(0,0,0,0.9822303921568627) 50%, rgba(3,0,41,1) 100%);
    z-index: 3;
}

.upperbar .leftpart{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    width: 50%;
}

.upperbar .rightpart{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 50%;
    background-color: rgba(255, 250, 240, 0);
}

.upperbar .rightpart a{
    font-family: Poetsen;
    font-size: 1.5vw;
    color: #FFFAFA;
    text-decoration: none;
    margin-left: 1vw;
    margin-right: 1vw;
    transition: all 0.8s ease;
    cursor: pointer;
}

a:hover {
    transform: scale(110%);
}

#name{
    color: #FFFAFA;
    text-align: center;
    font-size: 2vw;
    font-family: Poetsen;
    margin-left: 1vw;
}

#launguage{
    font-family: Poetsen;
    font-size: 1.0vw;
    color: #FFFAFA;
    position: absolute;
    right: 2vw;
    top: 50%;
    transform: translate(0%,-50%);
    transition: all 1s ease;
}

#launguage:hover{
    transform: translate(0%,-50%) rotate(360deg);
}

@keyframes float {
    0%{
        transform: translate(0%,0%) rotate(2deg);
    }
    50%{
        transform: translate(5%,1vh) rotate(-2deg);
    }
    100%{
        transform: translate(0%,0%) rotate(2deg);
    }
}